<template>
  <div>
    <div id="background"></div>
    <Menu mode="horizontal" theme="primary" active-name="1" @on-select="onSelect">
        <MenuItem name="0">
          <p style="font-size: 30px"><strong>元</strong>气<b>小</b>屋</p>
        </MenuItem>
        <MenuItem name="1">
            <Icon type="ios-paper"></Icon>
            首页
        </MenuItem>
        <MenuItem name="2" v-if="hasLogin">
            <Icon type="ios-people"></Icon>
            查看已经加入的小组
        </MenuItem>
        <MenuItem name="3">
            <Icon type="ios-people"></Icon>
            搜索小组
        </MenuItem>
        <Submenu name="4" id="login-or-logout">
            <template slot="title" v-if="!hasLogin">
                <Icon type="stats-bars"></Icon>
                未登录
            </template>
            <template slot="title" v-if="hasLogin">
                <Icon type="stats-bars"></Icon>
                {{username}}
            </template>
            <MenuGroup title="用户">
                <MenuItem name="3-1" v-if="!hasLogin">登陆</MenuItem>
                <MenuItem name="3-2" v-if="hasLogin">用户信息</MenuItem>
                <MenuItem name="3-3" v-if="hasLogin">修改密码</MenuItem>
                <MenuItem name="3-4" v-if="hasLogin">登出</MenuItem>
            </MenuGroup>
        </Submenu>
    </Menu>
    <div id="main">
      <router-view/>
    </div>
    <!-- <footer>
      <div>
        <ul>
          <li><a href="">了解我们</a></li>
          <li><a href="">帮助</a></li>
        </ul>
        <ul>
          <li>各种信息...</li>
          <li>各种信息...</li>
          <li>各种信息...</li>
        </ul>
      </div>
    </footer> -->
  </div>
</template>
<script>
import router from '../../router'
export default {
  data: function() {
    return {
      hasLogin: false,
      username: '',
      id: '',
    }
  },
  mounted: function() {
    let username = window.localStorage.getItem('username')
    let id = window.localStorage.getItem('id')
    if(username != null && id != null) {
      this.hasLogin = true
      this.username = username
      this.id = id
    }
  },
  
  methods: {
    onSelect: function(name) {
      if(name == '1') {
        router.push('/')
      }
      if(name == '3-1') {
        router.push('user/login')
      } else if(name == '3-2') {
        router.push('/home/user/profile')
      } else if(name == '3-3') {
        router.push('/home/changepassword')
      }
    }
  }
}
</script>
<style>
#background {
  background-color: #F9F9F9;
  height: 100%;
  position: fixed;
  width: 100%;
  z-index: -1;
}
p {
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
}
#login-or-logout {
  float:right;
}

</style>



