<template>
  <div id="app">
    <router-view v-on:login="login" :username="username"></router-view>
  </div>
</template>

<script>
export default {
  name: 'app',
  data: function() {
    return {
      username: '',
      id: '',
    }
  },
  methods: {
    login: function(data) {
      console.log("触发login函数保存字段")
      this.username = data.username
      this.id = data.id
      window.localStorage.setItem('username', data.username)
      window.localStorage.setItem('id', data.id)
    }
  }

}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



