<template>
  <div id="background">
    <div id="change-password-box">
      <h1><strong>修</strong>改密码</h1>
      <table>
        <tr>旧密码</tr>
        <tr><Input v-model="oldPassword" type="password" placeholder="请输入密码" style="width: 280px"></Input></tr>
        <tr>新密码</tr>
        <tr><Input v-model="firstNewPassword" type="password" placeholder="请输入新密码" style="width: 280px"></Input></tr>
        <tr>再次输入新密码</tr>
        <tr><Input v-model="secondNewPassword" type="password" placeholder="请再次输入新密码" style="width: 280px"></Input></tr>
        <tr><Button type="success" size="large" :disabled="change_password_box_submit_state" @click="change_password_box_submit">提交</Button></tr>
        <div id="message-box">
          小提示: 密码必须大于八位数
        </div>
      </table>
    </div>
  </div>
</template>
<script>
import router from '../../router'
import { changePassword } from '../../api/api'
export default {
  data: function() {
    return {
      oldPassword: '',
      firstNewPassword: '',
      secondNewPassword: '',
    }
  },
  methods: {
    change_password_box_submit: function() {
      if(this.firstNewPassword != this.secondNewPassword) {
        this.$Message.info('密码不一致')
        return
      }
      let options = {
        oldPassword: this.oldPassword,
        newPassword: this.newPassword,
        token: window.localStorage.getItem('token'),
        username: window.localStorage.getItem('username')
      }
      changePassword(options).then(res=>{
        if(res.status == 200) {
          if(res.data.status == 1) {
            this.$Message.info('修改成功')
            window.localStorage.clear()
            router.push('/login')
          } else {
            this.$Message.info('修改失败')
          }
        } else {
          this.$Message.info(`修改失败, 错误码:${res.status}`)
        }
      })

    }
  },
  computed: {
    change_password_box_submit_state: function() {
      if(this.oldPassword.length == 0 || this.firstNewPassword.length == 0 || this.secondNewPassword.length == 0) {
        return true
      }
      return false
    }
  }
}
</script>
<style>
#change-password-box {
  background-color: #FFFFFF;
  width: 50%;
  height: 600px;
  margin: 0 auto;
  margin-top: 30px;
  box-shadow:0 1px 2px rgba(0,0,0,0.3);
  border-radius: 5px;
}
#change-password-box table {
    margin: 0 auto;
    margin-top: 20px;
  }
#change-password-box tr {
  display: block;
  margin-bottom: 10px;
}
#message-box {
  margin-top: 20px;
  height: 60px;
}
#message-box p {
  line-height: 60px;
  font-size: 15px;
}
</style>


