<template>
  <div id="background">
    <div class="title">
      <h1><strong>登</strong>陆到 <strong>元</strong>气小屋</h1>
    </div>
    <div id="login-box">
      <form>
        <table>
          <tr class="first-title">员工账号</tr>
          <tr><Input v-model="user" placeholder="账号长度不得小于六位数" style="width: 280px"></Input></tr>
          <tr>密码</tr>
          <tr><Input v-model="password" type="password" placeholder="密码长度不得小于八位数" style="width: 280px"></Input></tr>
          <tr><Button type="success" size="large" :disabled="!userState || !passwordState" @click="login">登陆</Button></tr>
        </table>
      </form>
    </div>
    <div id="alert-box" v-if="alert_state">
      <Alert :type="alert_type" show-icon>{{alert_msg}}</Alert>
    </div>
    <div id="message-box">
      <p>忘记密码了?<router-link to="/user/forgetpassword">点击此处</router-link></p>
    </div>
    <div id="tool-box">
      <ul>
        <li><a href="">帮助</a></li>
        <li><a href="">了解我们</a></li>
      </ul>
    </div>
  </div>
  
</template>
<script>
import router from '../../router'
import { loginRequest } from '../../api/api'
export default {
  computed: {   
    userState: function() {
      let result = this.user.length < 6 ? false : true
      return result
    },
    passwordState: function() {
      let result = this.password.length < 8 ? false : true
      return result
    }
  },
  data() {
    return {
      user: '',
      password: '',
      msgBoxState: false,
      msgBoxVariant: 'danger',
      alertMsg: '',
      alert_state: false,
      alert_type: 'error',
      alert_msg: '',
    }
  },
  methods: {
    login: function() {
      console.log('login')
      let options = {
        username: this.user,
        password: this.password,
      }
      loginRequest(options)
        .then(res=>{
          if(res.status == 200) {
            if(res.data.status == 1) {
              this.$emit('login', {username: res.data.data.username, id: res.data.data.id})
              router.push('/home')
            } else {
              this.alert_state = true
              this.alert_msg = res.data.msg
            }
          } else {
            this.alert_state = true
            this.alert_msg = `未知错误, 返回码${res.status}`
          }
        })
    },
    changeLoginAlert(variant,state,msg) {
      this.msgBoxVariant = variant
      this.msgBoxState = state
      this.alertMsg = msg
    }
  }
}
</script>
<style>
  #background {
    background-color: #F9F9F9;
    height: 100%;
    position: fixed;
    width: 100%;
  }
  div {
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
  }
  h1 {
    font-family: "Microsoft YaHei";
  }
  h1 strong {
    font-size: 30px;
  }
  .title {
    margin-top: 30px;
  }
  #login-box, #message-box {
    background-color: #FFFFFF;
    width: 400px;
    margin: 0 auto;
    box-shadow:0 1px 2px rgba(0,0,0,0.3);
    border-radius: 5px;
  }
  #login-box table {
    width: 400px;
    padding-top: 20px;
    margin-top: 10px;
  }
  #login-box tr {
    display: block;
    margin-bottom: 10px;
  }
  #message-box {
    margin-top: 20px;
    height: 60px;
  }
  #message-box p {
    line-height: 60px;
    font-size: 15px;
  }
  #tool-box {
    margin-top: 20px;
  }
  #tool-box ul{
    width: 180px;
    display: block;
    margin: 0 auto;
  }
  #tool-box li {
    padding:0 20px;
    float: left;
  }
  #alert-box {
  margin: 0 auto;
  margin-top: 20px;
  width: 400px;
}
</style>


