<template>
  <div id="background">
    <div class="title">
      <h1><strong>忘</strong>记 <strong>密</strong>码</h1>
    </div>
    <div id="forget-box" v-if="state == 'forget-box'">
      <table>
        <tr>用户名</tr>
        <tr><Input v-model="username" placeholder="请先输入要修改密码的用户名" style="width: 280px"></Input></tr>
        <tr><Button type="success" size="large" @click="submitUsername">提交</Button></tr>
      </table>
    </div>
    <div id="question-box" v-if="state == 'question-box'">
      <table>
        <tr>密保问题</tr>
        <tr><p>{{securityQuestion}}</p></tr>
        <tr>回答</tr>
        <tr><Input v-model="answer"style="width: 280px"></Input></tr>
        <tr><Button type="success" size="large" @click="submitQuestion">提交</Button></tr>
      </table>
    </div>
    <div id="password-box" v-if="state == 'password-box'">
      <table>
        <tr>密码</tr>
        <tr><Input v-model="firstPassword" type="password" placeholder="请输入密码" style="width: 280px"></Input></tr>
        <tr>再次输入密码</tr>
        <tr><Input v-model="secondPassword" type="password" placeholder="请再次输入密码" style="width: 280px"></Input></tr>
        <tr><Button type="success" size="large" :disabled="password_box_submit_state" @click="submitPassword">提交</Button></tr>
      </table>
    </div>
    <div id="alert-box" v-if="alert_state">
      <Alert :type="alert_type" show-icon>{{alert_msg}}</Alert>
    </div>
    <div id="message-box">
      <p>小提示:请大家牢记自己的密保问题</p>
    </div>
    <div id="tool-box">
      <ul>
        <li><a href="">帮助</a></li>
        <li><a href="">了解我们</a></li>
      </ul>
    </div>
  </div>
</template>
<script>
import router from '../../router'
import { getForgetPasswordQuestion, postForgetPasswordQuestion, postNewPassword } from '../../api/api'
export default {
  data() {
    return {
      state: 'forget-box',
      alert_state: false,
      alert_type: 'error',
      alert_msg: '',
      username: '',
      answer: '',
      securityQuestion: '',
      firstPassword: '',
      secondPassword: '',

    }
  },

  methods: {
    submitUsername: function() {
      let options = {
        username: this.username,
      }
      getForgetPasswordQuestion(options)
        .then(res=>{
          this.alert_msg = res.data.msg
          console.log(res.data)
          if(res.status === 200) {
            if(res.data.status == 1) {
              window.localStorage.setItem('temp_username', this.username)
              this.alert_type = 'success'
              this.alert_state = true
              this.securityQuestion = res.data.data
              this.alert_state = false
              this.state = 'question-box'
            } else {
              this.alert_type = 'error' 
              this.alert_state = true
            }
          } else {
            this.alert_type = 'error'
            this.alert_msg = '未知原因'
            this.alert_state = true
          }
        })
      
    },
    submitQuestion: function() {
      let options = {
        question: this.securityQuestion,
        answer: this.answer,
        username: window.localStorage.getItem('temp_username'),
      }
      postForgetPasswordQuestion(options)
        .then(res=>{
          console.log(res.data)
          this.alert_msg = res.data.msg
          if(res.status === 200) {
            if(res.data.status === 1) {
              window.localStorage.setItem('temp_token', res.data.data)
              this.alert_type = 'success'
              this.alert_state = true
              this.state = 'password-box'
              this.alert_state = false
            } else {
              this.alert_type = 'error'
              this.alert_state = true
            }
          } else {
            this.alert_type = 'error'
            this.alert_msg = '未知原因'
            this.alert_state = true
          }
        })
    },
    submitPassword: function() {
      if(this.firstPassword != this.secondPassword) {
        this.alert_type = 'error'
        this.alert_msg = '两次密码不一致'
        this.alert_state = true
        return 
      }

      let options = {
        username: window.localStorage.getItem('temp_username'),
        newPassword: this.secondPassword,
        token: window.localStorage.getItem('temp_token'),
      }
      postNewPassword(options)
        .then(res=>{
          console.log(res.data)
          this.alert_msg = res.data.msg
          if(res.status === 200) {
            if(res.data.status === 1) {
              this.alert_type = 'success'
              this.alert_state = true
              router.push('/user/login')
            } else {
              this.alert_type = 'error'
              this.alert_state = true
            }
          } else {
            this.alert_type = 'error'
            this.alert_msg = '未知原因'         
            this.alert_state = true
          }
        })
    }
  },

  computed: {
    password_box_submit_state: function() {
      return this.firstPassword.length < 8 || this.secondPassword < 8 ? true : false
    }
  }
}
</script>
<style>
#background {
  background-color: #F9F9F9;
  height: 100%;
  position: fixed;
  width: 100%;
}
div {
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
}
strong {
  font-size: 30px;
}
#forget-box, #question-box, #message-box, #password-box {
  background-color: #FFFFFF;
  width: 400px;
  margin: 0 auto;
  box-shadow:0 1px 2px rgba(0,0,0,0.3);
  border-radius: 5px;
}
.title {
  margin-top: 30px;
}
#forget-box table,#question-box table , #password-box table{
  width: 400px;
  padding-top: 20px;
  margin-top: 10px;
}
#forget-box tr, #question-box tr, #password-box tr {
  display: block;
  margin-bottom: 10px;
}
#message-box {
    margin-top: 20px;
    height: 60px;
  }
#message-box p {
  line-height: 60px;
  font-size: 15px;
}
#tool-box {
  margin-top: 20px;
}
#tool-box ul{
  width: 180px;
  display: block;
  margin: 0 auto;
}
#tool-box li {
  padding:0 20px;
  float: left;
}
#alert-box {
  margin: 0 auto;
  margin-top: 20px;
  width: 400px;
}
</style>


